<template>
    <home-container title="Sell" default-href="/home/wallet" :with-header="step === 3 ? false : true">
        <div v-if="step === 1">
            <div class="row">
                <div class="col-8 mx-auto text-center">
                    <ion-text><p class='lead'>How much Bitcoin do you want to sell?</p></ion-text>
                </div>
            </div>
            <ion-label position='stacked'>Amount</ion-label>
            <ion-item class='border'  lines='none'>
                <ion-label style='padding-right:10px;border-right:1px solid #e0e5eb'>BTC</ion-label>
                <ion-input type='number' v-model.lazy="amount.in_btc" placeholder="Enter amount here"></ion-input>
            </ion-item>
            <ion-item class='border ion-margin-vertical' lines='none'>
                <ion-label style='padding-right:10px;border-right:1px solid #e0e5eb'>USD</ion-label>
                <ion-input readonly  v-model.lazy="amount.in_usd"></ion-input>
            </ion-item>
            <ion-label position='stacked'>Earnings</ion-label>
            <ion-item class='border' lines='none'>
                <ion-label style='padding-right:10px;border-right:1px solid #e0e5eb'>NGN</ion-label>
                <ion-input v-model.lazy="amount.in_ngn" readonly></ion-input>
            </ion-item>
            <ion-button strong @click.prevent="step++" :disabled="!amount.in_btc" class='ion-margin-horizontal my-3' expand="block">
                <ion-label>Send</ion-label>
            </ion-button>
            <div class="row">
                <div class="col-8 mx-auto text-center">
                    <ion-text class='d-flex flex-column'>
                        <small class='small'>Exchange  rate disclaimer</small>
                        <span class='small'>1 NGN is equal to {{naira_to_dollar}} USD</span>
                    </ion-text>
                </div>
            </div>
        </div>
        <div v-if="step === 2">
            <div class="row mb-3">
                <div class="col-8 mx-auto text-center">
                    <ion-text>
                        <p class="lead">Enter bank account details below</p>
                    </ion-text>
                </div>
            </div>
            <ion-label position="stacked">Bank</ion-label>
            <ion-item  class="border ion-margin-bottom" lines="none">
                <ion-select v-model='bank_details.bank' interface="action-sheet" placeholder="Select Bank">
                    <ion-select-option v-for="(i,index) in ['Access Bank','Accion Microfinance Bank','Ecobank Plc','FCMB','Fidelity Bank','First Bank of Nigeria','GTBank Plc','Keystone Bank','Stanbic IBTC Bank','Sterling Bank','United Bank for Africa','Wema Bank','Zenith Bank']"  :key='index'>{{i}}</ion-select-option>
                </ion-select>
            </ion-item>
            <ion-label  position="stacked">Account Number</ion-label>
            <ion-item class="border ion-margin-bottom" lines="none" :class="bank_details.acc_name ?'rounded border-success ok-field':''">
                <ion-input
                
                v-model.lazy="bank_details.acc_num"
                :clear-input="true" maxlength="10"
                placeholder="Enter 10-digit account number"
                ></ion-input>
                <ion-icon :class="bank_details.acc_name?'':'ion-hide'" slot='end' :icon='checkmarkOutline'></ion-icon>
            </ion-item>
            <div v-if='bank_details.acc_name'>
                <ion-label  position="stacked">Account Name</ion-label>
                <ion-item  class="rounded ion-margin-bottom border border-success ok-field" lines="none">
                    <ion-input v-model.lazy="bank_details.acc_name" readonly></ion-input>
                    <ion-icon slot='end' :icon='checkmarkOutline'></ion-icon>
                </ion-item>
            </div>
            <ion-button strong @click.prevent="step++" :disabled="!bank_details.acc_name" class='ion-margin-horizontal my-3' expand="block">
                <ion-label>Confirm</ion-label>
            </ion-button>
            <ion-item lines="none" mode='md' class='ion-margin-vertical'>
                <ion-checkbox slot='start' ></ion-checkbox>
                <ion-label class='ion-text-wrap' >Already having a linked bank account?<br>
                Click to activate your linked bank account
                </ion-label>
            </ion-item>
        </div>
        <div v-if="step === 3">
            <ion-item lines="none" class="ion-no-padding">
                <ion-button color="dark" slot="start" fill="clear" @click.prevent="previousStep">
                <ion-icon slot="icon-only" :icon="arrowBackOutline"></ion-icon>
                </ion-button>
                <ion-text slot="start"><h1 class="">Input OTP</h1></ion-text>
            </ion-item>
            <div class="row my-3">
                <div class="col-9 mx-auto text-center">
                <ion-text><p class="font-weight-light">
                    To complete transaction,<br />
                    Input OTP code sent to +234*******484
                    </p></ion-text>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <v-otp-input  @on-complete="handleOnComplete" inputClasses="otp-input" :numInputs="6" separator=" "></v-otp-input>
            </div>
            <div class="row my-4">
                <div class="col-9 mx-auto text-center">
                <ion-text
                    ><p class="">
                    Code Expires in,<br />
                    <strong>{{ countdown }}</strong>
                    </p></ion-text
                >
                </div>
            </div>
            <ion-button strong
                :disabled="!authorized"
                @click.prevent="processSend"
                class="ion-margin-horizontal my-4"
                expand="block"
            >
            <ion-label>Authorize</ion-label>
            </ion-button>
            <div class="my-4">
                <ion-text
                ><p class="d-flex flex-column align-items-end justify-content-end">
                    <ion-text>Didn't get the code?</ion-text>
                    <strong @click="countDownTick" class="text-primary"
                    >Request another code</strong
                    >
                </p></ion-text
                >
            </div>
        </div>
        <div style="min-height: 80vh" class="d-flex flex-column align-items-center justify-content-center ion-margin-horizontal text-center" v-if="step === 4">
            <ion-icon class="icon-container mb-5" style="font-size: 4rem" :icon="checkmarkOutline" ></ion-icon>
            <p class='lead text-center'>Hello Jacky <br>We are transferring money to<br> your bank</p>
            <p class='text-muted small ion-margin-vertical'> The sum of <strong>{{ amount.in_ngn }} NGN</strong> has been requested by you  to <strong class='ion-text-uppercase'>{{ bank_details.acc_name }},</strong>. We are now processing this transaction</p>
            <strong class='font-weight-bold ion-margin-vertical' style="font-size:0.7rem">Please note that transactions arrive usually within<br> two-business days </strong>
            <ion-button strong @click.prevent="handleProcess" class="ion-margin-horizontal my-4 w-100" expand="block">
                <ion-label>Proceed</ion-label>
            </ion-button>
            <a href='' @click.prevent='step=1'>Not Sure? Cancel</a>

            <ion-modal class='alert-modal ion-padding' :is-open="showAlert">
                <ion-content class=' d-flex flex-column justify-content-center align-items-center text-center' style='height:100%'> 
                    <ion-icon class="mt-5" style="font-size: 4rem;background-color: #aafaa4;border-radius: 50%;padding: 1rem;" :icon="checkmarkOutline" ></ion-icon>
                    <h1>Successfull</h1>
                </ion-content>
            </ion-modal>
        </div>
        
    </home-container>
</template>
<script>
import { defineComponent } from "vue";
import { checkmarkOutline,arrowBackOutline} from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController,IonModal } from "@ionic/vue";
import VOtpInput from 'vue3-otp-input';

    export default defineComponent({
        components: { VOtpInput, IonModal},
        data() {
            return {
                naira_to_dollar:410.16,
                btc_to_dollar:47186.2,
                 step: 1,
                 amount: {
                    in_btc: null,
                    in_usd: null,
                    in_ngn: null,
                },
                bank_details:{
                    bank:null,
                    acc_num:null,
                    acc_name:null,
                },
                countdown: null,
                timer: null,
                disableAuthorization: true,
                otp:null,
                showAlert:false,
            }
        },
        watch: {
            "amount.in_btc"(value) {
                this.amount.in_usd = (this.btc_to_dollar * Number(value)).toFixed(1);
                this.amount.in_ngn = (this.amount.in_usd * this.naira_to_dollar).toFixed(1);
            },
            "bank_details.acc_num"(value){
                this.bank_details.acc_name = null;
                if(value && value.length > 9){
                    this.checkAccountNumber();
                }
            },
            step(value){
                if (value === 3) {
                    this.countDownTick();
                }
            }
        },
        computed:{
            authorized(){
                return this.otp && !this.disableAuthorization
            }
        },
        setup() {
            const router = useRouter();
            return {
                router,checkmarkOutline,arrowBackOutline
            };
        },
        methods: {
            async checkAccountNumber() {
                
                 if (this.bank_details.acc_num && this.bank_details.acc_num.length > 8) {
                     let loading = await loadingController.create({
                         message: "Please wait...",
                     });
                    loading.present();
                    setTimeout(() => {
                        this.bank_details.acc_name = "Eiremiokhae Stanley";
                        loading.dismiss();
                    }, 1000);
                    
                }
            },
            previousStep() {
                this.step--;
                this.timer = null;
                this.disableAuthorization = true;
                this.otp = null;

            },
            countDownTick() {
                clearInterval(this.timer);
                this.disableAuthorization = false;
                let countDownDate = new Date().setMinutes(new Date().getMinutes() + 1);
                this.timer = setInterval(() => {
                    var now = new Date().getTime();
                    var distance = countDownDate - now;
                    var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
                    var seconds = Math.floor((distance % (1000 * 60)) / 1000);
                    minutes = minutes < 10 ? "0" + minutes : minutes;
                    seconds = seconds < 10 ? "0" + seconds : seconds;
                    if (distance < 2) {
                    clearInterval(this.timer);
                    this.disableAuthorization = true;
                    return;
                    }
                    this.countdown = minutes + ":" + seconds;
                }, 1000);
            },
            handleOnComplete($event){
                this.otp = $event;
                this.disableAuthorization = false;
                this.processSend();
            },
            async processSend(){
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.step++;
                    loading.dismiss();
                }, 1000);
            },
            async handleProcess(){
                this.showAlert = false;
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.showAlert = true;
                    loading.dismiss();
                    setTimeout(() => {
                        this.showAlert = false;
                        setTimeout(() => {
                            this.resetFields();
                            this.router.push("/home/wallet");
                        }, 100);
                    }, 1500);
                }, 1000);
            },
            resetFields() {
                this.bank_details = { bank: null, acc_name: null, acc_num: null };
                this.amount = { in_btc: null, in_usd: null, in_ngn: null };
                this.step = 1;
                this.countdown = null;
                this.timer = null;
                this.disableAuthorization = true;
                this.otp = null;
                this.showAlert = false;
            },
        }
});

</script>
<style >
.ok-field{
    color: #0f5132;
    --background: #d1e7dd;
    border: 2px solid #badbcc;
}
.alert-modal{
    --height:16rem;
    --backdrop-opacity:0.7;
}
</style>